    border-left: 2px solid #7B7B7B;;
    border-top: 2px solid #7B7B7B;;
    border-right: 2px solid #7B7B7B;
}

.square:focus {
    outline: none;
}

.kbd-navigation .square:focus {
    background: #ddd;
}

.game {
    display: flex;
    flex-direction: row;
}

.game-info {
    margin-left: 20px;
}

button {
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    border-radius: 4px;
    transition-duration: 0.4s;
  }

  button:hover {
    background-color: #49794b; /* Green */
    color: white;
  }